<template>
  <div>
   <p>this.$store.state Êù•Ëß¶ÂèëÁöÑ</p>
   <button @click="addV">+</button>
   <button @click="redV">-</button>
   <br>
   stateÊï∞ÊçÆ:{{this.$store.state.count}}Ôºõ
   <br />
   getters:{{this.$store.getters.getterstate}}
   <br />

   <h5>mapActions,mapGetters,mapStateÂÜôÊ≥ï</h5>
   <p>mutations:ÊåâÈíÆË∞ÉÁî®‰∫ã‰ª∂</p>
   <button @click="add">+</button>
   <button @click="red">-</button>
   <p>action:ÊåâÈíÆË∞ÉÁî®‰∫ã‰ª∂</p>
   <button @click="addF">+</button>
   <button @click="redF">-</button>
   <br>
   stateÊï∞ÊçÆ:{{count}}
   gettersÊï∞ÊçÆ:{{getterstate}}
  </div>
</template>

<script>
import {mapActions,mapGetters,mapState,mapMutations} from 'vuex';
  export default {
    methods:{
      addV() {
        // this.$store.commit('add') Ë∞ÉÁî®mutations ÁöÑ
        this.$store.dispatch("addF");
      },
      redV() {
        // this.$store.commit('red') Ë∞ÉÁî®mutations ÁöÑ
        this.$store.dispatch("redF");
      },
      ...mapMutations([
        'add',
        'red'
      ]), // Ë∞ÉÁî®vuex mutations ÈáåÈù¢ÊñπÊ≥ïÂêç ‚ù§Ô∏èviewÂ±ÇÁõ¥Êé•ÈÄöËøá this.add Â∞±ÂèØ‰ª•Ë∞ÉÁî®ÊñπÊ≥ï
      ...mapActions([
        'addF',
        'redF'
      ])// Ë∞ÉÁî®vuex actions ÈáåÈù¢ÊñπÊ≥ïÂêç ‚ù§Ô∏èviewÂ±ÇÁõ¥Êé•ÈÄöËøá this.addF Â∞±ÂèØ‰ª•Ë∞ÉÁî®ÊñπÊ≥ï
    },
    computed: {
      ...mapState([
        'count'
      ]),
      ...mapGetters([
        'getterstate'
      ])
    }
  }
</script>


