
import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

const store = new Vuex.Store({
  state:{
    count:1,
    pagetitle:0,
  },
  // Ê£ÄÊµãstateÁöÑÂèòÂåñÔºåÂ¶ÇÊûústareÂèòÂåñËøô‰∏™Â∞±Ëß¶Âèë
  getters:{
    getterstate(state) {
       return state.count+1;
    }
  },
  // ‰∫ã‰ª∂Ëß¶Âèë‰øÆÊîπstareÔºåÁî±commitË∞ÉÁî® üåü
  mutations:{
    add(state) {
       state.count +=1;
    },
    red(state) {
      state.count -=1;
    },
    tabactive(state,title) {
      state.pagetitle = title;

      console.log(state.pagetitle)
    } 
  },
  // Á±ª‰ººmethods
  actions:{ 
    addF(count) {// stareÈáåÈù¢ÁöÑcount
      count.commit('add');// ‰∫ã‰ª∂Ëß¶Âèë‰øÆÊîπstareÔºåÁî±commitË∞ÉÁî® üåü
    },
    redF(count) {
      count.commit('red');
    },
    tabactiveF(pagetitle,data) {
      pagetitle.commit('tabactive',data);
    }
  }
})

export default store


