<template>
  <div class="warp" v-show="show">
    <div
      v-for="(item,index) in btn" 
      :class="[`btnwap`,{active:index === activeindex}]" 
      :key="index" 
      @click="change(item.routername,item.router,item.name)">
      {{item.name}}
    </div>
  </div>
</template>

<script>
import {mapActions} from 'vuex'
export default {
  data() {
    return {
      activeindex:null, 
      show:true,
      btn:[
        {
          name:'È¶ñÈ°µ',
          routername:"Home",
          router:'/',
        },
        {
          name:'ÂÖ∂‰ªñ',
          routername:"Order",
          router:'/Order',
        },
      ]
    }
  },
  watch: {
    '$route': function(to, from){
      console.log(this.$route.name)
    }
  },
  mounted() {
    this.change(this.$route.name);
    this._Activetitle(this.$route.meta.title);
  },
  methods:{
    /**
     * @NAME Ë∑ØÁî±ÂêçÁß∞
     * @router Ë∑ØÁî±
     * */ 
    change(routername,router,name) {
      // this.$store.dispatch("tabactiveF",123)
      this.getActive(routername);
      this._Activetitle(name)
      /***
       * 
       * 1„ÄÅÂ£∞ÊòéÂºèÔºö 
       *  1) Ê†πÊçÆË∑ØÁî±Ë∑ØÂæÑÔºà/home/sort/detailÔºâË∑≥ËΩ¨ <router-link :to="{path: '/home/sort/detail', query:{id: 'abc'}}">ÁÇπÂáªÊü•ÁúãÂ≠êÈ°µÈù¢</router-link>
       *  2) Ê†πÊçÆË∑ØÁî±ÂêçÁß∞ÔºàdetailÔºâË∑≥ËΩ¨ <router-link :to="{name: 'detail', params:{id: 'abc'}}">ÁÇπÂáªÊü•ÁúãÂ≠êÈ°µÈù¢</router-link>
       * :to=""  ÂèØ‰ª•ÂÆûÁé∞ÁªëÂÆöÂä®ÊÄÅÁöÑ Ë∑ØÁî± Âíå ÂèÇÊï∞
       * 2„ÄÅÁºñÁ®ãÂºèÔºö
       * 1Ôºâ this.$router.push({path: '/home/sort/detail',query:{id: 'abc'}})
       * 2Ôºâ this.$router.push({name: 'detail',params:{id: 'abc'}})
       * Â§áÊ≥®Ôºö params Âíå query ‰º†ÂèÇÁöÑÂå∫Âà´Ôºö
       * 1„ÄÅparams‰º†ÂèÇÊó∂  ÂèÇÊï∞‰∏ç‰ºöÂá∫Áé∞Âú®urlÁöÑË∑ØÂæÑ‰∏äÈù¢Ôºå ‰ΩÜÊòØÂà∑Êñ∞È°µÈù¢Êó∂paramÈáåÈù¢ÁöÑÊï∞ÊçÆ‰ºöÊ∂àÂ§±
       * 2„ÄÅquery‰º†ÂèÇÊó∂   ÂèÇÊï∞Âá∫Áé∞Âú®urlÁöÑË∑ØÂæÑ‰∏äÈù¢Ôºå Âà∑Êñ∞È°µÈù¢Êó∂queryÈáåÈù¢ÁöÑÊï∞ÊçÆ‰∏çÂèò
       * 
       * */
      /**
       * 1„ÄÅrouter 
       * 2„ÄÅNAME != this.$route.name Á¶ÅÊ≠¢ÂÜçÊ¨°Ë∑≥ËΩ¨Êú¨È°µÈù¢ÔºåÂõ†‰∏∫‰ºöÂØºËá¥vuerouterÊä•Èîô
       * */ 
      if(router && routername != this.$route.name) {
        this.$router.push({path:router})
      }
    },
    /***
     * @name vuex‰º†ÂèÇÊï∞
     * 1„ÄÅÂ∞Ü `this._Activetitle()` Êò†Â∞Ñ‰∏∫ `this.$store.dispatch('tabactiveF')`
     * 2„ÄÅÂú®ÁªÑ‰ª∂‰∏≠ËøõË°åËøôÊ†∑Ë∞ÉÁî®this._Activetitle(1) Â∞ÜË¢´Êò†Â∞Ñ‰∏∫Â¶Ç this.$store.dispatch('tabactiveF', 1)
     * */ 
    ...mapActions({
      _Activetitle:'tabactiveF' 
    }),
    getActive(routername) {
      const _Active = {
        'Home':() => {
          this.activeindex = 0;
        },
        'Order':() => {
          this.activeindex = 1;
        },
        'default':() => {
          this.show = false;
        }
      }
      // Â¶ÇÊûúÈ°µÈù¢Ë∑≥ËΩ¨Âà∞Ê≤°Êúâ‰∏äÈù¢ÁöÑÊåáÂÆö this.btn Ë∑≥ËΩ¨ÁöÑË∑ØÁî±Â∞±ËøõË°åÈöêËóè
      if(routername) {
        _Active[routername]();
      }else{
        _Active['default']();
      }
    }
  }
  
};
</script>

<style scoped>
.warp {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 45px;
  line-height: 45px;
  box-shadow: 1px -2px 9px 2px #9c9a9a;
  display: flex;
}
.btnwap {
  flex: 1;
  background-color: #03A9F4;
  color: #ffffff !important;
}
.btnwap:active{
  background-color: #0d7eb1;
}
.btnwap.active {
  background-color: #0d7eb1;
}
</style>


